import React from "react";

export default function Card(props) {
  return (
    <div className="innerCard">
      <h4 className="mode">{props.mode.toUpperCase()}</h4>
      <h2 className="amount">
        {props.amount}
        <span className="month">{props.period}</span>
      </h2>
      <hr></hr>
      <div className="details">
        {props.details.map((element) => {
          return (
            <div className="detailLine" style={!element.isEnabled ? {opacity:"0.5"}: {}}>
              {element.isEnabled ? "✓ " + element.name : "⨯ " + element.name}
            </div>
          );
        })}
      </div>
      <button className="btn">
        <b>BUTTON</b>
      </button>
    </div>
  );
}
